//- Breakpoints
$screen-s-min: 320px;
$screen-m-min: 768px;
$screen-l-min: 992px;
$screen-xl-min: 1550px;

@mixin small {
    @media (max-width: #{($screen-m-min - 1px)}) {
        // 767-320
        @content;
    }
}

@mixin medium {
    @media (min-width:#{$screen-m-min}) and (max-width:#{($screen-l-min - 1px)}) {
        // 992-768
        @content;
    }
}

@mixin large {
    @media (min-width:#{$screen-l-min}) and (max-width:#{($screen-xl-min - 1px)}) {
        // 1549-1024
        @content;
    }
}

@mixin extra-large {
    @media (min-width:#{$screen-xl-min}) {
        // >1550
        @content;
    }
}
