* {
    box-sizing: border-box;
}
body {
    font-family: $default-font;
    font-size: $main-size;
}
.wrapper {
    max-width: 966px;
    margin: 0 auto;
}
ul {
    list-style-type: none;
    padding-inline-start: 0;
}

a {
    cursor: pointer;
    display: block;
    text-decoration: none;
}

a:hover,
a:focus {
    text-decoration: none;
}
