.content {
    background-color: $bg-color;
    box-shadow: inset 0 8px 20px #efefef;
    min-height: 600px;
    padding: 60px 15px 75px 15px;
    text-align: center;
    @include small {
        padding: 30px 0 30px 0;
    }

    @include medium {
        padding: 30px 0 30px 0;
    }

    .content-inner {
        font-size: 0;
    }

    .content-item {
        display: inline-block;
        font-size: $main-size;
        margin-bottom: 30px;
        margin-right: 30px;
        max-height: 292px;
        max-width: 30%;
        overflow: hidden;
        position: relative;

        &:last-child {
            margin-right: 0;
        }

        @include small {
            margin: 1px;
            max-width: 33%;
        }

        @include medium {
            margin: 10px;
        }

        &::before {
            background-color: rgba(0, 0, 0, 0.5);
            content: '';
            display: block;
            height: 100%;
            position: absolute;
            transform: scaleY(1);
            transition: all 5s cubic-bezier(0.075, 0.82, 0.165, 1);
            width: 100%;
        }

        &:hover::before {
            background-color: rgba(0, 0, 0, 0);
            transform: scaleY(0);
        }
    }

    .content-link {
        color: $text-color;
        display: block;
        width: 100%;
    }

    .video-block::after {
        background: url('../images/icon-cam.png');
        content: '';
        height: 30px;
        position: absolute;
        right: 30px;
        top: 15px;
        width: 38px;
    }

    .content-foto {
        width: 100%;
    }

    .content-info {
        display: block;
        left: 12%;
        position: absolute;
        text-align: left;
        top: 50%;
        transform: translate(0, -50%);
        width: 80%;
        @include small {
            display: none;
        }

        .content-today,
        .content-date {
            font-family: $content-main-font;
            line-height: 24px;
            width: 100%;
            @include medium {
                font-size: $small-size;
            }
        }

        .content-today li,
        .content-date li {
            display: inline-block;
        }

        .content-today {
            margin-bottom: 20px;
        }

        .day {
            width: 35%;
        }

        .likes,
        .comments,
        .video {
            font-family: $content-icons-font;
            padding-left: 30px;
            position: relative;
            width: 30%;
        }

        .likes::before {
            background: url('../images/icon-heart.png');
            content: '';
            height: 19px;
            left: 3px;
            position: absolute;
            top: 3px;
            width: 21px;
        }

        .comments::before {
            background: url('../images/icon-comments.png');
            content: '';
            height: 16px;
            left: 3px;
            position: absolute;
            top: 6px;
            width: 21px;
        }

        .video::before {
            background: url('../images/icon-triangle.png');
            content: '';
            height: 21px;
            left: 5px;
            position: absolute;
            top: 0;
            width: 17px;
        }
    }

    .content-uploaded {
        bottom: 15px;
        font-family: $uploaded-date-font;
        left: 50%;
        position: absolute;
        right: 0;
        transform: translateX(-50%);
        width: 80%;
        @include small {
            display: none;
        }

        @include medium {
            font-size: $medium-size;
        }
    }

    .more-btn {
        margin-top: 23px;
        text-align: center;
    }

    .btn {
        background-color: $bg-color;
        border: 1px solid $default-color;
        border-radius: 25px;
        box-shadow: none;
        color: $default-color;
        display: inline-block;
        font-size: $small-size;
        padding: 5px 12px;
        text-transform: uppercase;
        width: 124px;
        &:hover {
            box-shadow: 2px 2px 5px #000;
        }
    }
}
