.header {
    padding: 3px 15px 10px 15px;
    .header-inner {
        display: flex;
        @include small {
            flex-direction: column;
        }

        @include medium {
            flex-direction: column;
        }
    }

    .logo {
        min-width: 31%;
        text-align: center;
    }

    .header-block {
        min-width: 69%;
        padding-left: 28px;
        @include small {
            text-align: center;
        }

        @include medium {
            text-align: center;
        }
    }

    .main-title {
        margin: 35px 0 14px;
        .header-title {
            color: $primary-color;
            display: inline-block;
            font-family: $account-name;
            font-size: $account-size;
            letter-spacing: -1.4px;
        }

        .date-of-start {
            border: 1px solid $attention-color;
            border-radius: 3px;
            color: $attention-color;
            display: inline-block;
            font-family: $attention-font;
            font-size: $medium-size;
            margin-left: 15px;
            padding: 9px;
        }
    }

    .header-stats {
        margin-bottom: 21px;
        li {
            display: inline-block;
            letter-spacing: -0.34px;
            margin-right: 25px;
        }

        span {
            font-family: $subtitle;
            letter-spacing: -0.34px;
        }
    }

    .block-form {
        display: table;
        width: 100%;

        @include small {
            margin: 0 auto;
            width: 73%;
        }

        @include medium {
            margin: 0 auto;
            width: 63%;
        }

        .text-date {
            color: $primary-color;
            display: table-cell;
            font-family: $calendar-font;
            line-height: 24px;
            padding-bottom: 16px;
            vertical-align: middle;
        }

        .form-group {
            display: inline-block;
            margin-right: 25px;
            width: 182px;
            input::placeholder {
                color: $input-color;
                font-family: $calendar-input-font;
                font-size: $medium-size;
                letter-spacing: 0.35px;
            }

            .input-group-addon {
                padding: 7px 8px;
            }

            .input-group-addon {
                border-left: 0;
            }
        }
    }
}
