$font-faces: 'Tahoma', 'ProximaNova-Regular', 'ProximaNova-Semibold',
    'ProximaNova-Bold', 'MyriadPro-Regular', 'HelveticaNeue',
    'HelveticaNeue-Bold';

@each $font-face in $font-faces {
    @font-face {
        font-family: $font-face;
        src: url('/fonts/#{$font-face}.eot');
        src: url('/fonts/#{$font-face}.eot?#iefix') format('embedded-opentype'),
            url('/fonts/#{$font-face}.woff2') format('woff2'),
            url('/fonts/#{$font-face}.woff') format('woff'),
            url('/fonts/#{$font-face}.ttf') format('truetype');
    }
}

